.page__body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  text-transform: uppercase;
  color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.nav {
  box-sizing: border-box;
  margin: 0 50px;
  padding: 0;
  display: flex;
}

.nav__item, .nav__list {
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__link {
  box-sizing: border-box;
  color: #060b35;
  height: 60px;
  margin: 0 10px;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  line-height: 1;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__item:first-child .nav__link {
  margin-left: 0;
}

.nav__item:last-child .nav__link {
  margin-right: 0;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  width: 200px;
  transition: transform .3s;
}

.card__content, .card__rating, .card__price {
  display: flex;
}

.card__content, .card__information {
  flex-direction: column;
}

.card__rating, .card__price {
  justify-content: space-between;
}

.card__content {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  align-items: center;
  padding-top: 32px;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__information {
  color: #060b35;
  margin: 0 16px;
}

.card__title, .card__code {
  margin-top: 0;
}

.card__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  transition: color .3s;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
}

.card__rating {
  align-items: end;
  font-size: 10px;
}

.card__rating, .card__code {
  line-height: 14px;
}

.card__title, .card__price--amount {
  line-height: 18px;
}

.card__reviews {
  align-self: center;
}

.card__price {
  align-items: center;
  margin-top: 24px;
  margin-bottom: 16px;
}

.card__price--name, .card__price--amount, .card__reviews {
  margin: 0;
}

.card__price--name {
  color: #616070;
  text-align: left;
  font-size: 12px;
}

.card__price--amount, .card__button {
  font-weight: 700;
}

.card__price--amount {
  font-size: 16px;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-bottom: 16px;
  font-family: inherit;
  font-size: 14px;
  transition-property: background-color, color, border;
  transition-duration: .3s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:nth-child(-n+4) {
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

